import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ConfigurationService } from '../common/configuration.service';
import { InheritMetadata } from '../common/inherit-metadata';
import { AxisDefaultsComponentGenerated } from './axis-defaults.component.generated';

/**
 * The default options for all Chart axes. Accepts the options supported by `categoryAxis`, `valueAxis`, `xAxis`, and `yAxis`.
 */
@InheritMetadata()
@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'kendo-chart-axis-defaults',
  template: ''
})
export class AxisDefaultsComponent extends AxisDefaultsComponentGenerated {
  // Place custom properties here

  constructor(public configurationService: ConfigurationService) {
    super(configurationService);
  }
}



